span {
  display: inline-block;
  vertical-align: middle;
}

.Landing-Background {
    background-image: url(/static/media/raft-education-background.7e639a78.png);
    height: 100%;
    width: 1250px;
    right: 0;
    position: absolute;
    background-position: top right;
    background-size: cover;
}

.Search-Area {
  z-index: 2;
  margin-top: 100px;
  margin-left: 200px;
  position: absolute;
}

.Title {
  color: #3F44B5;
  font-family: Merriweather;
  font-weight: 700;
  font-size: 100px;
  position: relative;
  text-align: left;
  margin-bottom: -20px;
}

.Subtitle {
  color: #828282;
  font-family: Rubik;
  font-weight: 300;
  font-size: 18px;
