}

/* Mobile */
@media (max-width: 600px) {

  .Search-Area {
    z-index: 2;
    margin-left: 75px;
    position: absolute;
    margin-top: 10px;
  }

  .Title {
    color: #3F44B5;
    font-family: Merriweather;
    font-weight: 700;
    font-size: 100px;
    text-align: left;
    margin-bottom: -20px;
  }

  .Subtitle {
    color: #828282;
    font-family: Rubik;
    font-weight: 300;
    font-size: 18px;
    position: relative;
    text-align: left;
    margin-bottom: 20px;
  }

  .Button-Wrapper {
    margin-top: 30px;
    text-transform: none;
  }

  .Landing-Background {
      background-image: url(/static/media/raft_background_mobile.aaba681d.png);
      height: 100%;
      width: 100%;
      right: 0;
      background-position: bottom right;
  }
}

