}

/* Reviewer */

.reviewerWrapper {
	margin-bottom: 20px;
}
.reviewerWrapper .topBar {
	font-size: 14px;
}
.reviewerWrapper .bottomStats {
	margin-top: 15px;
	line-height: 15px;
	padding: 10px;
}
.reviewerWrapper .topBar .reviewerBar {
	line-height: 15px;
	vertical-align: middle;
	margin-right: 15px;
	display: inline-block;
}
.reviewerWrapper .topBar .reviewerBarTitle {
  color: #000000;
  font-weight: 500;
}
.reviewerWrapper .topBar .reviewerBar > img {
	height: 25px;
	width: 25px;
}
.reviewerWrapper .bottomStats .reviewerStatHeader {
	font-size: 14px;
	vertical-align: middle;
	margin-right: 15px;
	display: inline-block;
	color: #000000;
	font-weight: 500;
}
.reviewerWrapper .bottomStats .reviewerStatHeader > img {
	height: 20px;
	width: 20px;
}
.reviewerWrapper .bottomStats .reviewerStat {
	font-size: 14px;
	display: inline-block;
	margin-top: 5px;
	margin-right: 10px;
	color: #000000;
}
.reviewerWrapper .bottomStats .reviewerStatLabel {
	font-size: 14px;
	color: #828282;
	width:90px;
}


/* Review Score */

.reviewScoreWrapper {
	margin-bottom: 30px;
}
.reviewScoreWrapper .reviewOverallScore {
	width: 50px;
	height: 50px;
	border: 3px solid #3F44B5;
	border-radius: 20%;
	display: -webkit-flex;
	display: flex;
	margin-right: -5px;
}
.reviewScoreWrapper .reviewOverallScore .scoreText {
	color: #000000;
	margin: auto;
	font-size: 18px;
	font-weight: 700;
}
.reviewScoreWrapper .secondaryScoreWrapper {
	position: relative;
	width: 62px;
	height: 50px;
	margin-left: -10px;
	display: -webkit-flex;
	display: flex;
}
.reviewScoreWrapper .secondaryScoreWrapper .secondaryScore {
	color: #000000;
	margin: auto;
	padding-bottom: 15px;
	font-weight: 500;
}
.reviewScoreWrapper .secondaryScoreWrapper .secondaryScoreLabel {
	position: absolute;
	font-size: 12px;
	bottom: 0;
	left: 50%;
  	-webkit-transform: translate(-50%, 0%);
  	        transform: translate(-50%, 0%);
  	white-space: nowrap;
}


